
export interface InavListItem {
    id?: number;
    path?: string;
    name?: string;
    icon?: string;
    child?: NavListItem[];
    selected?: boolean;
}

export class  NavListItem implements InavListItem {
    public id?: number;
    public path?: string;
    public name?: string;
    public icon?: string;
    public child?: NavListItem[];
    public selected?: boolean;

    constructor(attr: InavListItem = {}) {
        this.id = attr.id;
        this.name = attr.name;
        this.path = attr.name;
        this.icon = attr.icon;
        this.child = (attr.child || []).map(item => new NavListItem(item));
        this.selected = attr.selected;
    }

}
