<template>
    <div class="nav-wrapper-share">
        <image src="https://fanyi-cdn.cdn.bcebos.com/static/translation/sprite/images/normal/index-sc413d90635_65ba9b0.png" class="user-img"/>

        <ul class="nav-list">
            <li v-for="nav in selfNavList" :key="nav.id" >
                <div  :class="[nav.selected ? 'active' : '', 'li-item']" 
                    @click="setCurNav(nav)"  
                    @mouseover="setCurModal(nav)"
                   @mouseout="closeModal(nav.id)" 
                    >
                    <div  class="nav-item-li" @click="setCurNav(nav)" :id="'nav-item-' + nav.id">
                            <i :class="[nav.icon, 'item-icon']"></i>
                            <span class="nav-title"> {{nav.name}}</span>
                            <i class="el-icon-caret-left arrow"></i>
                    </div>

                    <div class="modal-wrapper"  v-show="isShowModal(nav)" :id="'nav-modal-' + nav.id">
                            <div v-for="child in nav.child" :key="child.id" class="modal-item">
                                <span class="child-title">{{child.name}}</span>
                                <span v-for="subChild in child.child" :key="subChild.id" style="text-align: left;">
                                    <span
                                    :class="[subChild.selected ? 'subchild-title-active ' : '', 'subchild-title']"
                                    @click="setSubChild(nav, subChild)">
                                        {{subChild.name}}
                                    </span>
                                </span>
                            </div>
                    </div>

                </div>
            </li>
        </ul>
    </div>
</template>
<script>
import {NavListItem} from './nav-model';

export default ({
    name: 'ZhNav',
    props: ['navlist'],
    data() {
        return {
            curNavIncdex: 1,
             curNav: {},
             navKeyWord: {bill: ''},
             selfNavList: this.navlist ? this.navlist  : []
        }
    },
    created() {
        console.log('nav-start');
        console.log(this.navList);
        console.log(this.selfNavList)
    },
    methods: {
        curRoute(path) {
            const curPath = this.$route.fullPath;
            return path == curPath;
        },
        curNavActive(name) {
            const curPath= this.$route.fullPath;
            let keyword = '';
            for(const key  in this.navKeyWord){
                if (this.navKeyWord && this.navKeyWord[key] == name) {
                    keyword = key;
                }
            }
            return  keyword ? curPath.startsWith('/' + keyword) : false;
        },
        isShowModal(nav) {
            return this.curNav && this.curNav.id === nav.id && nav.child && !!nav.child.length;
        },
        setCurNav(nav) {
            this.curNavIncdex = nav && nav.id;
            if (nav && nav.path) {
                this.$router.push({
                    path: nav.path
            }).catch(err => {err});
            }
        },
        setCurModal(nav) {
            this.curNav = nav;
            this.setModalOffsetTop(nav.id);
        },

        setSubChild(nav, subNav) {
            event.stopPropagation();
            this.closeModal(nav.id);
            this.$router.push({
                path: subNav.path
            }).catch(err => {err});
        },

        setModalOffsetTop(curCategoryId) {
            const navBut = document.querySelector(`#nav-item-${curCategoryId}`);
            const navModal = document.querySelector(`#nav-modal-${curCategoryId}`);
            const butTop = navBut && navBut.getClientRects()[0].top;
            const modalHeight = navBut && navBut.getClientRects()[0].bottom - navBut.getClientRects()[0].top;
            const resBottom  = document.body.offsetHeight - butTop;
            if (resBottom < modalHeight) {
                navModal['style'].bottom = `0px`;
            } else {
                navModal['style'].top = `${butTop - 5}px`;
                navModal['style'].bottom = `auto`;
            }
            //   navModal['style'].opacity = 1;
            navModal['style'].display = `flex`;
        },

        closeModal(navId) {
            this.curNav = new NavListItem();
            const navModal = document.querySelector(`#nav-modal-${navId}`);
            if (navModal) {
                // navModal['style'].opacity = 0;
                navModal['style'].display = `none`;
            }
        }
    }
})
</script>
