




import {Vue, Component, Prop } from 'vue-property-decorator';
import {Message} from 'element-ui';
@Component({})
export default class ZhMessage extends Vue {
    @Prop({default: {}}) message: {type: string, message: string, duration: number};
    created() {
        console.log('message-start');
    }
    mounted() {
      this.tip();
    }
     tip() {
            const message: any = this.message;
            Message({
                    type: message.type,
                    message: message.message
            });
        }
}
